.logo2 {
    width: 100px;
    height: 80px;
    color: #ffff;
    vertical-align: middle; 
    margin-top: 0px; 
}
.bar {
  grid-row: 1/2;
}
 a {
 text-decoration: none;
 color: #EBECED;
 }
.nav-bar{
    color: #EBECED;
    background-color: #787982;
    display: flex;
    justify-content: space-between;
    position: fixed;
    position: -webkit-sticky;
    width:100%;
    height: 5rem;
    flex-shrink: 01;
    flex-direction: column;
    top: 0;
    padding-bottom: 1px;
    border-bottom: 2px  #fefefe solid;
    z-index: 999;
    font-family: 'Raleway', sans-serif;
    font-weight: 800;
    font-size: 3em;
    letter-spacing: 4px;
}

.nav-links {
    display: flex;
    justify-content: space-evenly;;
    vertical-align: middle;
    margin-top: -1em;
    margin-left: 2em;
   
}
.nav-item {
    position: absolute;
    list-style: none;
    cursor: pointer;
    text-decoration: none;
    font-size: .75rem;
    font-weight: 550;
    padding-right: 1px;
    margin-left: 5px;
    margin-right: 5px;
    text-transform: uppercase;
    }
   
    .nav-item.b-line {
        display: inline-block;
        position: relative;
        color: #eaeef0;
     
      }
    
      .nav-item.b-line:after {
    content: '';
    position: absolute;
    width:100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 36.5px;
    left: 0;
    background-color: #ffffff;
    transform-origin: bottom left;
    transition: transform 0.25s ease-out;
      }
  
    
      .nav-item.b-line:hover:after {
        transform: scaleX(1);
        transform-origin: bottom left;
      }
  
      .toggleNav{
        color:rgb(229, 243, 243);
        font-size: 20px;
        visibility: hidden;
        }
  
        .nav-bar-toggle {
          visibility: hidden;
        }
  
      

   
    
  



